<template>
  <v-card class="pa-4">
    <h2>Ratio Calculator by Closed Files (Insurer)</h2>
    <v-row dense>
      <v-col cols="12" md="3">
        <v-select
          v-model="selectedDeptGroup"
          :items="deptGroupOptions"
          label="Department Group"
          dense
          outlined
          required
        />
      </v-col>
      <v-col cols="12" md="3">
        <v-select
          v-model="selectedInsurer"
          :items="insurerOptions"
          item-text="name"
          item-value="id"
          label="Insurer"
          dense
          outlined
          required
        />
      </v-col>
      <v-col cols="12" md="3">
        <v-menu v-model="menuMonth" :close-on-content-click="false">
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="selectedMonth"
              label="Month"
              prepend-icon="mdi-calendar"
              readonly
              v-bind="attrs"
              v-on="on"
              dense
              required
            />
          </template>
          <v-date-picker
            v-model="selectedMonth"
            type="month"
            @change="menuMonth = false"
          />
        </v-menu>
      </v-col>
      <v-col cols="12" md="3" class="d-flex align-end">
        <v-btn
          color="success"
          @click="exportExcel"
          :disabled="!selectedDeptGroup || !selectedInsurer || !selectedMonth"
        >
          <v-icon left>mdi-file-excel</v-icon>
          Export Excel
        </v-btn>
      </v-col>
    </v-row>
    <v-divider class="my-4" />
    <v-alert v-if="alertMessage" type="warning" outlined>
      {{ alertMessage }}
    </v-alert>
    <v-alert v-else type="info" outlined>
      Select department group, insurer, and month, then click "Export Excel" to
      generate the report.
    </v-alert>
  </v-card>
</template>

<script>
import moment from "moment";
import InsDataService from "../../services/InsDataService";
import { apiConfig } from "../../config/api.config";

export default {
  name: "RatioCalculatorInsurer",
  data() {
    return {
      deptGroupOptions: [
        { value: "TPBI", text: "TPBI" },
        { value: "MISC", text: "MISC" },
      ],
      selectedDeptGroup: null,
      insurerOptions: [],
      selectedInsurer: null,
      selectedMonth: null,
      menuMonth: false,
      alertMessage: "",
    };
  },
  methods: {
    async loadInsurers() {
      try {
        const res = await InsDataService.getAll();
        this.insurerOptions = res.data.sort((a, b) =>
          a.name.localeCompare(b.name)
        );
      } catch {
        this.insurerOptions = [];
      }
    },
    async exportExcel() {
      this.alertMessage = "";
      const params = {
        deptGroup: this.selectedDeptGroup,
        insurerId: this.selectedInsurer,
        month: moment(this.selectedMonth).format("YYYY-MM"),
      };
      const query = Object.entries(params)
        .map(([k, v]) => `${k}=${encodeURIComponent(v)}`)
        .join("&");
      const url = `${apiConfig.baseURL}/compliance/ratio-calculator-insurer/export?${query}`;
      try {
        const res = await fetch(url, { method: "GET" });
        // If response is JSON, check for alert
        const contentType = res.headers.get("content-type");
        if (contentType && contentType.includes("application/json")) {
          const data = await res.json();
          if (data.alert) {
            this.alertMessage = data.alert;
            return;
          }
        }
        // Otherwise, open as file
        window.open(url, "_blank");
      } catch (err) {
        this.alertMessage =
          err.message || "Failed to export Excel. Please try again.";
      }
    },
  },
  mounted() {
    this.loadInsurers();
  },
};
</script>

<style scoped></style>
